import { useEffect, useState } from "react";
import { Switch } from "./components/switch";
import logo from "./logo.png";
import { Message, MessageResponse, MessageTypes } from "./types";
import { useRecorder } from "./hooks";

function App() {
  const [audio, setAudio] = useState(false);
  const [video, setVideo] = useState(false);
  const { start, stop, state, getBlobs } = useRecorder();

  useEffect(() => {
    chrome.storage.sync.get(["audio"], function (result) {
      if (chrome.runtime.lastError) {
        console.error(chrome.runtime.lastError);
      } else {
        console.log(result);
        setAudio(result.audio);
      }
    });

    chrome.storage.sync.get(["video"], function (result) {
      if (chrome.runtime.lastError) {
        console.error(chrome.runtime.lastError);
      } else {
        console.log(result);
        setVideo(result.video);
      }
    });
  }, []);

  function handleAudioChange() {
    setAudio((prev) => !prev);
    chrome.storage.sync.set({ audio: !audio }, function () {
      console.log("Audio is set to " + !audio);
    });
  }

  function handleVideoChange() {
    setVideo((prev) => !prev);
    chrome.storage.sync.set({ video: !video }, function () {
      console.log("Video is set to " + !video);
    });
  }

  const handleRecord = () => {
    // showPopupDiv.show = false;
    const message: Message = {
      type: MessageTypes.getStream,
    };

    chrome.runtime.sendMessage(message, (response: MessageResponse) => {
      if (!response.success) {
        // showPopupDiv.show = true;
        return;
      }
    });
  };

  return (
    <div className="w-80 p-6">
      <div className="flex gap-2 items-center">
        <img src={logo} className="h-7 w-7" alt="logo" />
        <h1 className="font-bold text-[#120B48]">HelpMeOut</h1>
      </div>

      <div>
        <p className="mt-4 text-sm">
          This extension helps you record and share help videos with ease.
        </p>
      </div>

      <div className="space-y-6 mt-6">
        <div className="p-3 border border-[#100A42] rounded-xl flex justify-between w-full">
          <div className="flex items-center gap-2">
            <svg
              width="22"
              height="16"
              viewBox="0 0 22 16"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M14.75 6.5L19.4697 1.78033C19.9421 1.30786 20.75 1.64248 20.75 2.31066V13.6893C20.75 14.3575 19.9421 14.6921 19.4697 14.2197L14.75 9.5M3.5 14.75H12.5C13.7426 14.75 14.75 13.7426 14.75 12.5V3.5C14.75 2.25736 13.7426 1.25 12.5 1.25H3.5C2.25736 1.25 1.25 2.25736 1.25 3.5V12.5C1.25 13.7426 2.25736 14.75 3.5 14.75Z"
                stroke="#0F172A"
                strokeWidth="1.5"
                strokeLinecap="round"
                strokeLinejoin="round"
              />
            </svg>

            <span className="font-medium">Camera</span>
          </div>

          <div>
            <Switch checked={video} onCheckedChange={handleVideoChange} />
          </div>
        </div>

        <div className="p-3 border border-[#100A42] rounded-xl flex justify-between w-full">
          <div className="flex items-center gap-2">
            <svg
              width="24"
              height="24"
              viewBox="0 0 24 24"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M12 18.75C15.3137 18.75 18 16.0637 18 12.75V11.25M12 18.75C8.68629 18.75 6 16.0637 6 12.75V11.25M12 18.75V22.5M8.25 22.5H15.75M12 15.75C10.3431 15.75 9 14.4069 9 12.75V4.5C9 2.84315 10.3431 1.5 12 1.5C13.6569 1.5 15 2.84315 15 4.5V12.75C15 14.4069 13.6569 15.75 12 15.75Z"
                stroke="#0F172A"
                strokeWidth="1.5"
                strokeLinecap="round"
                strokeLinejoin="round"
              />
            </svg>

            <span className="font-medium">Audio</span>
          </div>

          <div>
            <Switch checked={audio} onCheckedChange={handleAudioChange} />
          </div>
        </div>

        <button
          type="button"
          className="bg-[#100A42] font-medium py-4 w-full rounded-xl text-white"
          onClick={handleRecord}
        >
          {state === "recording" ? "Stop Recording" : "Start Recording"}
        </button>
      </div>
    </div>
  );
}

export default App;
